import React, { useContext, useEffect, useState } from "react";
import { ButtonGroup, Modal, ToggleButton } from "react-bootstrap";
import { ErrorsContext, SmartTradesContext } from "../../../App";
import { fetchDetails } from "../../../state/smartTrades/SmartTradesEffects";
import "./SmartTradeDetails.css";
import SmartTradeDetailsStatusTable from "./SmartTradeDetailsStatusTable/SmartTradeDetailsStatusTable";
import SmartTradeDetailsTable from "./SmartTradeDetailsTable/SmartTradeDetailsTable";

const purposeRadios = [
  { name: "Position", value: "position" },
  { name: "Take profit", value: "take_profit" },
  { name: "Reduce funds", value: "reduce_funds" },
  { name: "Stop loss", value: "stop_loss" },
  { name: "Panic", value: "panic" },
  { name: "All", value: "" },
];

const SmartTradeDetails = ({
  modalIsOpen,
  setModalIsOpen,
  smartTradeId,
}: any) => {
  const { smartTradesState, smartTradesDispatch } =
    useContext(SmartTradesContext);
  const { smartTrades } = smartTradesState;
  const smartTrade = smartTrades[smartTradeId];
  const { errorsDispatch } = useContext(ErrorsContext);

  const [purpose, setPurpose] = useState("");
  const [trades, setTrades] = useState([]);

  useEffect(() => {
    if (smartTradeId) {
      fetchDetails(
        smartTradesDispatch,
        smartTradesState,
        errorsDispatch,
        smartTradeId
      ).then();
    }
  }, [smartTradeId]);

  useEffect(() => {
    if (smartTrade && smartTrade.trades) {
      setTrades(smartTrade.trades);
    }
  }, [smartTrade]);

  useEffect(() => {
    if (purpose) {
      setTrades(
        smartTrade.trades.filter(
          (trade: any) => trade.trade_purpose === purpose
        )
      );
    } else {
      if (smartTrade && smartTrade.trades) {
        setTrades(smartTrade.trades);
      }
    }
  }, [purpose]);

  return (
    <>
      {smartTrade && (
        <Modal
          show={modalIsOpen}
          onHide={() => {
            setModalIsOpen("");
          }}
          scrollable
          size="xl"
        >
          <Modal.Header closeButton>
            <Modal.Title>{`${smartTrade.pair
              .split("_")
              .reverse()
              .join("/")} (Trade ID: ${smartTradeId})`}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div>
              <div className="SmartTradeDetailsControl">
                <SmartTradeDetailsStatusTable smartTradeId={smartTradeId} />
              </div>
              <div className="SmartTradeDetailsControl">
                <ButtonGroup>
                  {purposeRadios.map((radio, idx) => (
                    <ToggleButton
                      key={idx}
                      id={`detail-position-range-radio-${idx}`}
                      type="radio"
                      variant="outline-primary"
                      name="radio-detail-position"
                      value={radio.value}
                      checked={purpose === radio.value}
                      onChange={(e) => setPurpose(e.currentTarget.value)}
                    >
                      {radio.name}
                    </ToggleButton>
                  ))}
                </ButtonGroup>
              </div>
            </div>
            {trades && (
              <SmartTradeDetailsTable
                smartTradeId={smartTradeId}
                trades={trades}
              />
            )}
          </Modal.Body>
        </Modal>
      )}
    </>
  );
};

export default SmartTradeDetails;
